<template>
  <div class="templet">
    <div class="cameraBox">
      <div class="buttoBox">
        <img :src="camera" alt="">
      </div>
      <p></p>
    </div>
    <div class="user">
      <section class="name">
        <label for=""></label>
        <input type="text" name="" v-model="itemName">
      </section>
      <van-cell @click="hospitalGroup" title="" is-link :value="activeHospital" />
      <section class="name">
        <label for=""></label>
        <input type="text" name="" v-model="itemName">
      </section>
      <section class="name">
        <label for=""></label>
        <input type="text" name="" v-model="itemName">
      </section>
    </div>
    <div class="user">
      <van-cell title="" is-link :value="activeHospital" />
      <section class="name">
        <label for=""></label>
        <input type="text" name="" v-model="itemName">
      </section>
    </div>
    <div class="user">
      <h2></h2>
      <textarea name="name" placeholder="..." v-model="fwjs"></textarea>
      <p>{{fwjs.length}}/200</p>
    </div>
    <div class="user">
      <h2></h2>
      <textarea name="name" placeholder="..." v-model="fwjs"></textarea>
      <p>{{fwjs.length}}/200</p>
    </div>
    <div class="user">
      <h2></h2>
      <textarea name="name" placeholder="..." v-model="fwjs"></textarea>
      <p>{{fwjs.length}}/200</p>
    </div>
    <button type="button" name="button" class="zdymb"><img :src="addZi" alt=""></button>
    <button type="button" name="button" :class="next ? 'next' : 'none'"></button>
    <van-popup v-model="showhos" position="bottom">
      <van-picker :columns="hospital" :show-toolbar="true" @cancel="cancel" @confirm="onChange" />
    </van-popup>
  </div>
</template>

<script>
  import {
    camera
  } from "../assets/base64.js"

  export default {
    name: 'templet',
    data() {
      return {
        camera: camera(),
        itemName: '',
        hospital: [''],
        showhos: false,
        activeHospital: '',
        fwjs: ''
      }
    },
    methods: {
      hospitalGroup() {
          this.showhos = true
      },
      onChange(value) {
          this.activeHospital = value
          this.showhos = false
      },
      cancel() {
          this.showhos = false
      },
    }
  }
</script>

<style lang="scss">
.van-cell__title{
  display: flex;
  align-items: center;
}
.van-cell__value{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 74px;
  color: #999999;
}
.van-cell__right-icon{
  right: 38px;
}
</style>

<style lang="scss" scoped>
  @import '../assets/css/templet.scss';
</style>



