import Vue from 'vue'
import Router from 'vue-router'
import nonShop from '@/pages/nonShop' //
import setUpShop1 from '@/pages/setUpShop1' //1
import setUpShop2 from '@/pages/setUpShop2' //2
import setUpShop3 from '@/pages/setUpShop3' //3
import register1 from '@/pages/register1' //1
import register2 from '@/pages/card' //2
import register3 from '@/pages/register3' //3
import setAccount from '@/pages/setAccount' //
import xbCenter from '@/pages/xbCenter' //
import xbSet from '@/pages/xbSet' //
import footer from '@/pages/footer' //nav
import index from '@/pages/index' //
import shopDetail from '@/pages/shopDetail' //
import shopEdit from '@/pages/shopEdit' //
import income from '@/pages/income' //
import PresentRecord from '@/pages/PresentRecord' //
import withdrawDeposit from '@/pages/withdrawDeposit' //
import SubmittedSuccessfully from '@/pages/SubmittedSuccessfully' //
import chioceNurse from '@/pages/chioceNurse' //
import AddServiceItems from '@/pages/AddServiceItems' //
import templet from '@/pages/templet' //
import addItemSuccess from '@/pages/addItemSuccess' //
import addShopAddress from '@/pages/addShopAddress' //

import server from '@/pages/server'
import cancel from '@/pages/cancel'
import success from '@/pages/success'
import login from '@/pages/login' //

import orderList from '@/pages/orderList'
import sendOrder from '@/pages/sendOrder'
import chooseNurse from '@/pages/chooseNurse'
import message from '@/pages/message'


import shopIcon from '@/pages/photoIcon'
import selectCity from '@/pages/selectCity'

Vue.use(Router)

export default new Router({
  routes: [{ //b
    path: '/',
    component: footer,
    children: [{
      path: '/',
      name: 'index',
      component: index
    }]
  }, {
    path: '/nonShop',
    name: 'nonShop',
    component: nonShop
  }, {
    path: '/setUpShop1',
    name: 'setUpShop1',
    component: setUpShop1
  }, {
    path: '/setUpShop2',
    name: 'setUpShop2',
    component: setUpShop2
  }, {
    path: '/setUpShop3',
    name: 'setUpShop3',
    component: setUpShop3
  }, {
    path: '/register1',
    name: 'register1',
    component: register1
  }, {
    path: '/register2',
    name: 'register2',
    component: register2
  }, {
    path: '/register3',
    name: 'register3',
    component: register3
  }, {
    path: '/setAccount',
    name: 'setAccount',
    component: setAccount
  }, {
    path: '/xbCenter',
    name: 'xbCenter',
    component: xbCenter
  }, {
    path: '/xbSet',
    name: 'xbSet',
    component: xbSet
  }, {
    path: '/shopDetail/:shop_id',
    name: 'shopDetail',
    component: shopDetail
  }, {
    path: '/shopEdit',
    name: 'shopEdit',
    component: shopEdit
  }, {
    path: '/income',
    name: 'income',
    component: income
  }, {
    path: '/PresentRecord',
    name: 'PresentRecord',
    component: PresentRecord
  }, {
    path: '/SubmittedSuccessfully',
    name: 'SubmittedSuccessfully',
    component: SubmittedSuccessfully
  }, {
    path: '/chioceNurse',
    name: 'chioceNurse',
    component: chioceNurse
  }, {
    path: '/templet',
    name: 'templet',
    component: templet
  }, {
    path: '/addItemSuccess',
    name: 'addItemSuccess',
    component: addItemSuccess
  }, {
    path: '/AddServiceItems',
    name: 'AddServiceItems',
    component: AddServiceItems
  }, {
    path: '/withdrawDeposit',
    name: 'withdrawDeposit',
    component: withdrawDeposit
  }, {
    path: '/server/:order_no',
    name: 'server',
    component: server //
  }, {
    path: '/cancel/:order_no',
    name: 'cancel',
    component: cancel //
  }, {
    path: '/success/:order_no',
    name: 'success',
    component: success //
  }, {
    path: '/login', //
    name: 'login',
    component: login
  }, {
    path: '/orderList', //
    name: 'orderList',
    component: orderList,
  }, {
    path: '/sendOrder/:order_no', //
    name: 'sendOrder',
    component: sendOrder,
  }, {
    path: '/chooseNurse/:order_id/:shop_id/:nurse_id', //
    name: 'chooseNurse',
    component: chooseNurse,
  }, {
    path: '/addShopAddress', //
    name: 'addShopAddress',
    component: addShopAddress
  }, {
    path: '/shopIcon', //
    name: 'shopIcon',
    component: shopIcon
  }, {
    path: '/selectCity', //
    name: 'selectCity',
    component: selectCity
  }, {
      path: '/message',//
      name: 'message',
      component: message,
  }]
})



