<template>
  <div class="income">
    <img src="../assets/images/updateSuccess.png" alt="">
    <h2></h2>
    <p></p>
    <button @click="userInfo" type="button" name="button"></button>
  </div>
</template>

<script type="text/javascript">
  export default {
    methods: {
      userInfo: function() {
        this.$router.push({
          path: '/'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/updateSuccess.scss';
</style>



