<template>
  <div class="withdrawDeposit">
    <div class="main">
      <h2></h2>
      <div class="inputBox">
        <span></span><input type="number" v-model="txje">
      </div>
      <p v-if="txje === ''"><span> {{jine}}</span><i @click="qb"></i></p>
      <p v-else><span> {{jine - txje}}</span><i @click="qb"></i></p>
    </div>
    <button type="button" name="button" :class="wait === true ? 'wait' : ''"></button>
  </div>
</template>

<script type="text/javascript">
  export default {
    data() {
      return {
        jine: 368,
        txje: '',
        wait: true
      }
    },
    methods: {
      qb() {
        this.txje = this.jine
      }
    },
    watch: {
      txje: function (e) {
        console.log(e);
        if(e !== ''){
          this.wait = false
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/withdrawDeposit.scss';
</style>



